.page {
  width: 100%;
  height: 100%;
  position: absolute;
  min-height: 660px;
  min-width: 380px;
}

.container {
  width: 100%;
  will-change: contents;
  height: 90%;
  position: absolute;
  opacity: 0;
  margin: 0 auto;
  z-index: 1;
  transform-style: preserve-3d;
  animation: fadeIn 1s forwards;
  animation-delay: 1s;
}